import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';


import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AddStuffPageModule } from '../pages/add-stuff/add-stuff.module';
import { ListStuffPageModule } from '../pages/list-stuff/list-stuff.module';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    AddStuffPageModule,
    ListStuffPageModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-stuff/add-stuff.module#AddStuffPageModule', name: 'AddStuffPage', segment: 'add-stuff', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-stuff/list-stuff.module#ListStuffPageModule', name: 'ListStuffPage', segment: 'list-stuff', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



